:root { 
  --alto: #d9d9d9;
  --azure-radiance: #0075ff;
  --black: #000000;
 
  --font-size-m: 18.4px;
 
  --font-family-inter-regular: "Inter-Regular", Helvetica;
}
.inter-regular-normal-black-18-4px {
  color: var(--black);
  font-family: var(--font-family-inter-regular);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 400;
}
